import React, { Component } from 'react';
import "./List.css";
import PropTypes from "prop-types";


class Form extends Component {
    static propTypes = {
        addContact: PropTypes.func
    }
    state = {
        name: "",
        phone: ""
    }
    onChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value,
        })
    }
    onSubmit = (e) => {
        e.preventDefault();
        if (e.target.firstChild.value.length > 3 && this.state.phone.length > 3) {
            this.props.addContacts({
                ...this.state
            })
        }
        this.setState({
            name: "",
            phone: ""
        })
    }
    render() {
        return (
            <div className="forms">
                <div className="form-alt">
                    <form onSubmit={this.onSubmit}>
                        <input name="name" id="name" value={this.state.name} onChange={this.onChange} placeholder="Name"></input>

                        <input name="phone" id="phone" value={this.state.phone} onChange={this.onChange} placeholder="Phone"></input>

                        <button>Add </button>
                    </form>
                </div>
            </div>
        )
    }
}
export default Form;