import '../App.css';
import React from "react";
import Contacts from "./Contacts"
// import React, { useState, useEffect } from "react";
var getStory = JSON.parse(localStorage.getItem("getStory"))


class App extends React.Component {
  state = {
    contacts: getStory !== null ? getStory : getStory !== undefined ? [{ name: "", phone: "" }] : getStory !== '' ? getStory : ""
  }

  addContacts = (Form_dan) => {




    const { contacts } = this.state

    contacts.push(Form_dan)
    localStorage.setItem("getStory", JSON.stringify(contacts))

    this.setState({
      contacts
    })
  }
  componentDidUpdate() {
    document.getElementById("basarili").classList.add("success")
    setTimeout(() => {
      document.getElementById("basarili").classList.remove("success")
    }, 3000);
  }
  render() {
    return (
      <div className="App" >
        <div className="mesajbox">
          <div id="basarili" className="">  </div>
        </div>
        <Contacts
          addContacts={this.addContacts}
          contacts={this.state.contacts}
        />

      </div>
    );
  }

}

export default App;
