import React, { Component } from 'react'
import "./List.css";
import PropTypes from "prop-types";

class List extends Component {
    state = {
        filters: ""
    }
    static propTypes = {
        contacts: PropTypes.array.isRequired,
    }

    onChangefilter = (e) => {

        this.setState({
            filters: e.target.value
        })
    }

    listDel = (e) => {
        var listeler = JSON.parse(localStorage.getItem("getStory"))
        var names = document.querySelectorAll(".name")
        // var liste = listeler.indexOf(e.target.innerText);

        for (var [a, user] of listeler.entries()) {
            if (user.name == e.target.firstChild.innerText) {
                listeler.splice(a, 1);

            }
        }
        localStorage.setItem("getStory", JSON.stringify(listeler))

        if (e.target.className === "sil") {
            e.target.remove();
        }

    }



    render() {
        if (this.props.contacts) {
            var contactList = this.props.contacts.filter((contac) => {
                return contac.name.toLowerCase().indexOf(
                    this.state.filters.toLowerCase()
                ) !== -1
            })

        }
        contactList.reverse()

        return (
            <div className="listArea" >
                <label htmlFor="filter">Filter</label>
                <input name={"filter"} value={this.state.filter} onChange={this.onChangefilter} id="filter" placeholder="Filter of name" />

                <ul className="List">
                    {
                        contactList ?
                            contactList.map(contact =>
                                <li key={contact.phone} onClick={this.listDel} className="sil">
                                    <span className="name" >{contact.name}</span>
                                    <span className="tel">{contact.phone}</span>

                                </li>
                            )
                            : ""
                    }

                </ul>
            </div>
        )
    }
}
export default List;
